import { MdSnackBar, MD_DIALOG_DATA, MdDialogRef } from '@angular/material';
import { BackendService } from './../../../backend.service';
import { Component, OnInit, Inject } from '@angular/core';

@Component({
  selector: 'app-citas-detail',
  templateUrl: './citas-detail.component.html',
  styleUrls: ['./citas-detail.component.scss']
})
export class CitasDetailComponent implements OnInit {

  private loader: boolean = false;
  private cita: any = {};

  constructor(
    @Inject(MD_DIALOG_DATA) public data: any,
    private ref: MdDialogRef<CitasDetailComponent>,
    private back: BackendService,
    private snack: MdSnackBar
  ) { }

  ngOnInit() {
    this.loader = true;
    this.back.getCitaDetail(this.data.id).subscribe(
      data => {
        this.loader = false;
        this.cita = data;
      },
      err => {
        this.loader = false;
        this.snack.open('Error, no se pudo cargar la cita.', '', {duration: 4000});
        this.ref.close({err});
      }
    );
  }

}



